.home-container {
  position: relative;
  height: 100%;
  width: 100%; }
  .home-container img {
    position: relative;
    z-index: 1;
    -webkit-user-select: none; }
  .home-container img:hover ~ .refresh-btn {
    display: block; }
  .home-container .refresh-btn:hover {
    display: block; }
