    -webkit-user-select: none;
    display: none;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 50%;
    margin-left: -44px;
    margin-top: -44px;
    font-size: 40px;
    color: #fff;
    width: 88px;
    line-height: 88px;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    cursor: pointer; }
  .home-container .loading {
    display: none;
    position: absolute;
    top: 0;
    height: 100%;
    left: 0;
    width: 100%;
    z-index: 11;
