    background-color: #50cf77;
    background-size: 64px 64px; }
    .home-container .loading.show {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center; }
    .home-container .loading::after {
      content: "Loading...";
